@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400;900&display=swap');
body {
  margin: 0;
  padding: 0;
  background: #1F1F1F;
  color: transparent;
  font-family: 'Inconsolata', monospace;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
* {
  user-select: none;
}
.word {
  position: absolute;
  cursor: grab;
  font-size: 30px;
  color: #FDCB12;
}
.word.highlighted {
  font-weight: bold;
  color: #F7F7F7;
}
a {
  text-decoration: none;
  color: #adadad;
  display: block;
  padding: 1rem;
}